.table-col{
    margin: auto;
    width: 40%;
    padding: 10px; 
}
td,th {
    border: 1px solid rgb(190, 190, 190);
    padding: 10px; 
}
td {
    text-align: center; 
    background-color:coral;
    
}
table {
    border-collapse: collapse;
    border: 2px solid rgb(200, 200, 200); 
    letter-spacing: 1px;
    font-family: sans-serif;
    font-size: 0.8rem;
}
.table-col{
    background-color:darkgoldenrod;
}