body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.head{
    border: 2px white;
    background-color: aquamarine;
    opacity: 0.7;
    border-radius: 10px;
    max-width: 400px;
    height: 100%;
    margin: auto;
    margin-top: 10%;
    padding: 10px;
    text-align: center;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}
#title{
    font-family: 'Times New Roman', Times, serif;
}
.form-group{
    padding: 10px;
    margin: auto;
}
input{
    margin-left: 20px;
}
.btn{
    background-color: hotpink;  
    border-radius: 12px;
    padding: 5px 68px; 
    margin: 10px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}