body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.icons {
    color: white;
    font-size: 7em;

}
.employee {
    border: 2px rgb(255, 255, 255);
    background-color: rgb(17, 9, 12);
    opacity: 0.7;
    border-radius: 10px;
    max-width: 250px;
    height: 300px;
    margin: auto;
    margin-top: 10%;
    padding: 20px;
    text-align: center;
    color: white;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}